import React from 'react';
import AppCard from '../../../../../@crema/core/AppCard';
import TransactionTable from './TransactionTable';
import AppSelect from '../../../../../@crema/core/AppSelect';
import { useIntl } from 'react-intl';
// import {TransactionData} from '../../../../types/models/dashboards/Analytics';
export interface TableData {
  _id: string;
  user_ip: string;
  country: string;
  device: string;
  track_id: string;
}

interface Props {
  tabledata: TableData[];
}

const OrderNTransaction: React.FC<Props> = ({ tabledata }) => {
  const handleSelectionType = (data: any) => {
    console.log('data: ', data);
  };
  const { messages } = useIntl();
  return (
    <AppCard
      height={1}
      contentStyle={{ paddingLeft: 0, paddingRight: 0 }}
      title={'Mac User List'}
      action={
        <AppSelect
          menus={[
            messages['dashboard.thisWeek'],
            messages['dashboard.lastWeeks'],
            messages['dashboard.lastMonth'],
          ]}
          defaultValue={messages['dashboard.thisWeek']}
          onChange={handleSelectionType}
        />
      }>
      <TransactionTable transactionData={tabledata} />
    </AppCard>
  );
};

export default OrderNTransaction;
